import React from "react";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  useTheme,
  IconButton,
  Tooltip,
} from "@mui/material";
import AddShoppingCartIcon from "@mui/icons-material/AddShoppingCart";

export default function ProductList({productList, onProductClick, onAddToCart }) {
  const theme = useTheme();


  return (
    <TableContainer component={Paper} sx={{ backgroundColor: theme.palette.background.paper }}>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Name</TableCell>
            <TableCell>Description</TableCell>
            <TableCell>Price</TableCell>
            <TableCell align="center">Cart</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {productList.map((p, index) => (
            <TableRow
              key={p.id || index}
              onClick={() => onProductClick(p)}
              sx={{
                cursor: "pointer",
                "&:hover": { backgroundColor: theme.palette.action.hover },
              }}
            >
              <TableCell>{p.name}</TableCell>
              <TableCell>{p.description}</TableCell>
              <TableCell>{p.price}</TableCell>
              <TableCell align="center">
                <Tooltip title="Add to Cart">
                  <IconButton
                    color="primary"
                    size="small"
                    onClick={(e) => {
                      e.stopPropagation(); 
                      onAddToCart({ ...p, quantity: 1 });
                    }}
                  >
                    <AddShoppingCartIcon />
                  </IconButton>
                </Tooltip>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
